import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient, HttpErrorResponse } from '@angular/common/http';
import { BehaviorSubject, Observable, of, throwError } from 'rxjs';
import { State } from '../models/state.model';
import { tap, catchError, finalize } from 'rxjs/operators';
import { EmpresaXBureau } from '../models/empresa-bureau.model';
import { HandleError, HttpErrorHandler } from './http-error-handler.service';
import { ToastrService } from 'ngx-toastr';

import { environment } from '../../environments/environment';
import { Bureau } from '../models/bureau.model';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

function compare(v1, v2) {
  return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

function sort<EmpresaXBureau>(items: EmpresaXBureau[], column: string, direction: string): EmpresaXBureau[] {
  if (direction === '') {
    return items;
  } else {
    return [...items].sort((a, b) => {
      const res = compare(a[column], b[column]);
      return direction === 'asc' ? res : -res;
    });
  }
}

function fullMatch(empresas: EmpresaXBureau, cdBureau: string, cdEmpresa: string) {
  return (empresas.cdEmpresa.toString() === cdEmpresa && empresas.cdBureau.toString() === cdBureau);
}

function validItemB(empresas: EmpresaXBureau, cdEmpresa: string, cdBureau: string) {
  if (empresas.cdBureau.toString() === cdBureau) {
    if (empresas.cdEmpresa.toString() === cdEmpresa) {
      return empresas
    }
  }
}

@Injectable({
  providedIn: 'root'
})
export class EmpresaBureauService {

  private _serviceName = "EmpresaXBureau";
  //private serviceUrlBureau = 'politica';
  //private serviceUrl = 'empresa/bureau'
  private listaBureaus: Bureau[] = [];
  private listaEmpBureau: Array<EmpresaXBureau> = [];
  private lista: Array<EmpresaXBureau> = [];
  private _bureaus$ = new BehaviorSubject<Bureau[]>([]);
  private _empresaBureaus$ = new BehaviorSubject<EmpresaXBureau[]>([]);
  private _loading$ = new BehaviorSubject<boolean>(false);
  private handleError: HandleError;

  private originalCopy: EmpresaXBureau = null;

  private _state: State = {
    page: 1,
    pageSize: 10,
    codigo: '',
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  };

  constructor(private http: HttpClient,
    private httpErrorHandler: HttpErrorHandler,
    private toastr: ToastrService) {
    this.handleError = httpErrorHandler.createHandleError(this._serviceName);
    this.loadBureaus();
  }

  get bureaus$() { return this._bureaus$.asObservable(); }
  get items$() { return this._empresaBureaus$.asObservable(); }
  get loading$() { return this._loading$.asObservable(); }

  set codigo(codigo: string) { this._set({ codigo }); }

  private _set(patch: Partial<State>) {
    Object.assign(this._state, patch);
  }

  getResourceName(): string {
    return 'empresa-bureau';
  }

  validItem(cdEmpresa: string, cdBureau: string) {
    let items = this.listaEmpBureau.filter(item => validItemB(item, cdEmpresa, cdBureau));
    return items.length;
  }

  load(cdBureau: number): void {
    this.getBureaus(cdBureau).pipe(catchError(this.handleError<EmpresaXBureau[]>('Empresa Bureau', [])))
      .subscribe(items => {
        this.listaEmpBureau = items;
        this._empresaBureaus$.next(items);
      });
  }

  getBureaus(cdBureau: number): Observable<EmpresaXBureau[]> {
    this._loading$.next(true);
    console.log(this._serviceName + " Lista de items selecionados");
    return this.http.get<EmpresaXBureau[]>(`${environment.serviceUrls.empresaBureauUrl}/listarEmpresa/${cdBureau}`)
      .pipe(
        /* tap(
           data => this.log(`${this._serviceName}/load`, data),
           error => {
             console.log('Error')
             this.logError(`${this._serviceName}/load`, error)
           }
         ),*/
        finalize(() => this._loading$.next(false)),
        catchError(this.handleError<EmpresaXBureau[]>('Carregar itens', []))
      );
  }

  loadTodos(): void {
    this.getTodasEmpresas().pipe(catchError(this.handleError<EmpresaXBureau[]>('Empresa Bureau', [])))
      .subscribe(items => {
        this.listaEmpBureau = items;
        this._empresaBureaus$.next(this.listaEmpBureau);
      });
  }

  getTodasEmpresas(): Observable<EmpresaXBureau[]> {
    this._loading$.next(true);
    console.log(this._serviceName + " Lista de items selecionados");
    return this.http.get<EmpresaXBureau[]>(`${environment.serviceUrls.empresaBureauUrl}/listarTodos/`)
      .pipe(
        //tap(_ => console.log(`fetched items/`)),
        finalize(() => this._loading$.next(false)),
        catchError(this.handleError<EmpresaXBureau[]>('getTodasEmpresas', []))
      );

  }

  loadBureaus(): void {
    this.getTodosBureaus().pipe(catchError(this.handleError<Bureau[]>('Empresa Bureau', [])))
      .subscribe(items => {
        this.listaBureaus = items;
        this._bureaus$.next(this.listaBureaus);
      });
  }

  getTodosBureaus(): Observable<Bureau[]> {
    console.log(this._serviceName + " Lista de items selecionados");
    return this.http.get<Bureau[]>(`${environment.serviceUrls.bureauUrl}/listaBureau/`)
      .pipe(
        //tap(_ => console.log(`fetched items`)),
        finalize(() => this._loading$.next(false)),
        catchError(this.handleError<Bureau[]>('getTodosBureaus', []))
      );
  }

  startNewItem(): Observable<EmpresaXBureau> {
    let selectedItem: EmpresaXBureau = new EmpresaXBureau();
    this.originalCopy = null;
    return of(selectedItem);
  }


  selectItem(cdBureau: string, cdEmpresa: string): Observable<EmpresaXBureau> {
    if (cdEmpresa === null || cdEmpresa === undefined) {
      console.log(`${this._serviceName}: cÃ³digo da empresa invÃ¡lido!`);
      let msg = `CÃ³digo da empresa escolhido Ã© invÃ¡lido!`;
      this.toastr.error(msg, "CyberMessenger");
      return of(null);
    }
    let selectedItem: EmpresaXBureau = this.listaEmpBureau.find(item => fullMatch(item, cdBureau, cdEmpresa));
    this.originalCopy = JSON.parse(JSON.stringify(selectedItem));
    return of(selectedItem);
  }

  rollbackChanges() {
    if (this.originalCopy === null) return;
    let selectedIndex = this.listaEmpBureau.findIndex(item => fullMatch(item, this.originalCopy.cdBureau.toString(), this.originalCopy.cdEmpresa.toString()));
    this.listaEmpBureau[selectedIndex] = this.originalCopy;
    this._empresaBureaus$.next(this.listaEmpBureau);
  }

  save(items: EmpresaXBureau[], done): void {
    this._loading$.next(true);
    let retVal: Observable<EmpresaXBureau[]> = this.http.post<EmpresaXBureau[]>(environment.serviceUrls.empresaBureauUrl, items, httpOptions);

    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(changedList => { if (changedList['entity'] == null) console.log(`${this._serviceName}: Ocorreu um erro durante a atualizaÃ§Ã£o do item.`);}),
      catchError(this.handleError<EmpresaXBureau[]>('Salvar'))
      //`postItems cÃ³digo=${items.length}`
    ).subscribe(response => {
      if (response) {
        let msg = `Item alterado com sucesso!`;
        this.toastr.success(msg, "CyberMessenger");
        this._empresaBureaus$.next(this.listaEmpBureau);
        done();
      };
    }, error => {
      let msg = `Ocorreu uma falha na atualizaÃ§Ã£o dos dados. Tente novemente!`;
      this.toastr.error(msg, "CyberMessenger");
      console.log('Erro ao salvar registro: ' + error.error.message);
    });
  }

  remove(cdEmpresa: number, cdBureau: number, done): void {
    this._loading$.next(true);
    let retVal: Observable<EmpresaXBureau> = this.http.delete<EmpresaXBureau>(`${environment.serviceUrls.empresaBureauUrl}/${cdEmpresa},${cdBureau}`, httpOptions);

    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(changed => { if (changed) console.log(`${this._serviceName}: Registro removido com sucesso!`); }),
      catchError(this.handleError<EmpresaXBureau>('Remover'))
    ).subscribe(response => {
      if (response) {
        let itemIndex = this.listaEmpBureau.findIndex((item) => item['cdEmpresa'] === cdEmpresa);
        if (itemIndex >= 0) {
          this.listaEmpBureau.splice(itemIndex, 1);
          console.log(`Item [${cdEmpresa}] removido com sucesso`);
        }
        let msg = `Item removido com sucesso!`;
        this.toastr.success(msg, "CyberMessenger");
        this._empresaBureaus$.next(this.listaEmpBureau);
        done();
      };
    }, error => {
      let msg = `Ocorreu uma falha ao atualizar empresa ${cdEmpresa}. Tente novemente!`;
      this.toastr.error(msg, "CyberMessenger");
      console.log('Erro ao remover registro: ' + error.error.message);
    });
  }

  include(item: EmpresaXBureau, done): void {
    //return this.http.put(`${environment.serviceUrls.empresaBureauUrl}/${item[keyName]}`, item, httpOptions).pipe(
    this._loading$.next(true);
    let retVal: Observable<EmpresaXBureau> = this.http.put<EmpresaXBureau>(`${environment.serviceUrls.empresaBureauUrl}`, item, httpOptions)

    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(data => { if (data['entity'] === null) console.log(`Ocorreu um erro durante a incluisÃ£o do item cÃ³digo=${item['cdEmpresa']}/${item['nomeEmpresa']}.`); }),
      catchError(this.handleError<EmpresaXBureau>('Incluir'))
    ).subscribe(response => {
      if (response) {
        console.log(`${this._serviceName}: Item cÃ³digo=${response['entity']['cdEmpresa']}/${response['entity']['nomeEmpresa']} incluido com sucesso!`);
        let msg = `Empresa ${response['entity']['nomeEmpresa']} salvo com sucesso!`;
        this.toastr.success(msg, "CyberMessenger");

        //this.listaEmpBureau.push(item);
        this.listaEmpBureau.unshift(response['entity']);
        this._empresaBureaus$.next(this.listaEmpBureau);
        done();
      }
    }, error => {
      let msg = `Ocorreu uma falha ao atualizar Empresa ${item.cdEmpresa}. Tente novemente!`;
      this.toastr.error(msg, "CyberMessenger");
      console.log('Erro ao remover registro: ' + error.error.message);
    });
  }

  /**
     * Handle Http operation that succeded.
     * @param operation - name of the operation that succeded
     * @param result - Result value to return as the observable
     */
  private log<Bureau>(operation: string, result: Bureau) {
    // TODO: better job of transforming error for user consumption
    console.log(`${operation} succeded`);

    // Let the app keep running by returning an empty result.
    //return of(result as EmpresaXBureau);
    return result;
  }

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private logError(operation: string, result: HttpErrorResponse) {
    // TODO: send the error to remote logging infrastructure
    if (result.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', result.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${result.status}, ` +
        `body was: ${result.error}`);
    }

    // TODO: better job of transforming error for user consumption
    console.log(`${operation} failed: ${result.message}`);

    // Let the app keep running by returning an empty result.
    //return of(result as EmpresaXBureau);
    return throwError(
      'Something bad happened; please try again later.');
  }


}
