import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { EmpresaBureauComponent } from './empresa-bureau.component';
import { EmpresaBureauListComponent } from './list/empresa-bureau-list.component';
import { EmpresaBureauEditComponent } from './editor/empresa-bureau-edit.component';
import { ProjectConfigResolver } from './interceptors/project.config.resolver';

@NgModule({
  imports: [
      RouterModule.forChild([
        {path: '', component: EmpresaBureauComponent,
          children: [
            {path: '', component: EmpresaBureauListComponent},
            {path: ':openType',component: EmpresaBureauEditComponent},
            {path: ':openType/:code',component: EmpresaBureauEditComponent }
          ],resolve: {
            envConfiguration: ProjectConfigResolver
          }}
    ])
  ],
  exports: [RouterModule]
})
export class EmpresaBureauRoutingModule { }
