import { Component, OnInit } from '@angular/core';
import { EmpresaBureauService } from '../services/empresa-bureau.service';
import { DecimalPipe } from '@angular/common';
import { faPlus, faPencilAlt, faTrashAlt } from '@fortawesome/free-solid-svg-icons';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';
import { EmpresaXBureau } from '../models/empresa-bureau.model';
import { Observable } from 'rxjs';
import { Bureau } from '../models/bureau.model';

@Component({
  selector: 'empresa-bureau-list',
  templateUrl: './empresa-bureau-list.component.html',
  styleUrls: ['../empresa-bureau.component.scss']
})
export class EmpresaBureauListComponent implements OnInit {

  title = '';
  resourceName = '/';
  faPlus = faPlus;
  faPencilAlt = faPencilAlt;
  faTrashAlt = faTrashAlt;
  selected: any = "";
  bureaus$: Observable<any>;
  items$: Observable<any>;
  static LOCALE_KEY = 'currentLocale';

  constructor(private route: ActivatedRoute,
              private router: Router,
              private empresaBureauService: EmpresaBureauService,
              private toastr: ToastrService,
              public translate: TranslateService) {
        this.items$ = this.empresaBureauService.items$;
        this.bureaus$ = this.empresaBureauService.bureaus$;
        this.resourceName = '/external-'+ this.empresaBureauService.getResourceName();
        this.selected = "Todos";
        
        translate.setDefaultLang('ptBR');
        const currentLocale =  sessionStorage.getItem(EmpresaBureauListComponent.LOCALE_KEY);
        
        translate.setDefaultLang(currentLocale);
        
    }

  ngOnInit() {
    this.empresaBureauService.loadTodos();  
  }

  create(): void {
    this.router.navigate([this.resourceName, 'create']);
  }

  change(indexId:string): void {
    this.router.navigate([this.resourceName, 'change', indexId]);
  }

  remove(indexId:string): void {
    this.router.navigate([this.resourceName, 'remove', indexId]);
  }
  valueChange(bureau: any){
    
    if(bureau.cdBureau == undefined){
      console.log(" Item todos foi selecionado ");
      this.selected = "Todos"
      this.empresaBureauService.loadTodos();  
    }else{
      console.log("selected value",bureau.cdBureau , 'value of selected',this.selected);
      this.selected = bureau.descBureau;
      this.empresaBureauService.load(bureau.cdBureau);
    }
  }
  //this.selected = event.target.value;
}
