import { Component, OnInit } from '@angular/core';
import { EmpresaXBureau } from '../models/empresa-bureau.model';
import { Observable, Subscription } from 'rxjs';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Bureau } from '../models/bureau.model';
import { EmpresaBureauService } from '../services/empresa-bureau.service';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'empresa-bureau-edit',
  templateUrl: './empresa-bureau-edit.component.html',
  styleUrls: ['../empresa-bureau.component.scss']
})
export class EmpresaBureauEditComponent implements OnInit {

  items$: Observable<any>;
  selected: any = 1880;
  subtitle: string;
  resourceId: string;
  
  openType: string = "";

  goBackLink: string;
  content: EmpresaXBureau;

  errors: string;

  bureaus$: Observable<any>;
  static LOCALE_KEY = 'currentLocale';

  contentForm = new FormGroup({
    "cdEmpresa": new FormControl('', [
      Validators.required,
      Validators.maxLength(4)
    ]),
    "nomeEmpresa": new FormControl('', [
      Validators.required,
      Validators.maxLength(30)
    ]),
    "cnpj": new FormControl('', [
      Validators.required,
      Validators.maxLength(15)
    ]),
    "cdBureau": new FormControl('', [
      Validators.required,
      Validators.maxLength(15)
    ]),
    "nomeBureau": new FormControl('', [
      Validators.required,
      Validators.maxLength(4)
    ]),
    "codEmpXBur": new FormControl('', [
      Validators.required,
      Validators.maxLength(8)
    ]),
    "nmEmpXBur": new FormControl('', [
      Validators.required,
      Validators.maxLength(30)
    ]),
    "qtLimInfNeg": new FormControl('', [
      Validators.required,
      Validators.maxLength(8)
    ]),
    "qtLimSupNeg": new FormControl('', [
      Validators.required,
      Validators.maxLength(8)
    ]),
    "qtLimInfRea": new FormControl('', [
      Validators.required,
      Validators.maxLength(8)
    ]),
    "qtLimSupRea": new FormControl('', [
      Validators.required,
      Validators.maxLength(8)
    ]),

  });

  private empresaBureauSubs: Subscription = null;
  constructor(private route: ActivatedRoute,
    private router: Router,
    private empresaBureauService: EmpresaBureauService,
    private toastr: ToastrService,
    public translate: TranslateService) {

    translate.setDefaultLang('ptBR');
    const currentLocale = sessionStorage.getItem(EmpresaBureauEditComponent.LOCALE_KEY);

    translate.setDefaultLang(currentLocale);
    this.bureaus$ = this.empresaBureauService.bureaus$;
    this.goBackLink = '/external-' + this.empresaBureauService.getResourceName();
  }

  ngOnInit() {

    let paramMap = this.route.snapshot.paramMap;

    if (paramMap.has("openType"))
      this.openType = paramMap.get('openType');
    if (paramMap.has("code"))
      this.resourceId = this.route.snapshot.paramMap.get('code');

    if (!this.openType) {
      console.log("OpenType estÃ¡ nulo");
      this.toastr.error("Erro ao identificar o tipo de operaÃ§Ã£o. Tente novamente!", "Erro:");
      this.exit();
      return;
    }

    this.subtitle = 'EMPRESA-BUREAU.ITEMSUBTITLE.' + this.openType.toUpperCase();
    this.errors = '';

    if (this.resourceId === null || this.resourceId == undefined ) {
      this.empresaBureauSubs = this.empresaBureauService.startNewItem()
        .subscribe(empresaBureau => {
          this.content = empresaBureau;
          this.contentForm.patchValue(this.content);
        });
    } else
    this.empresaBureauSubs = this.empresaBureauService.selectItem(this.resourceId.substr(0,1),this.resourceId.substr(1))
        .subscribe(
          item => {
            this.content = (!item && item === null) ? this.content : item;
            this.contentForm.patchValue(this.content);
          });
  }
  ngOnDestroy(){
    console.log('Cleaning EmpresaBureau...');
    if (this.empresaBureauSubs != undefined) this.empresaBureauSubs.unsubscribe();
  }
  valueChange(event) {
    console.log("selected value", event.target.value,
      'value of selected', this.selected);
    this.content.cdBureau = +event.target.value;
  }

  save() {
    switch (this.openType) {
      case 'remove':
        this.empresaBureauService.remove(this.content.cdEmpresa, this.content.cdBureau, () => this.exit());
        /*this.empresaBureauService.remove(this.content.cdEmpresa,this.content.cdBureau).subscribe(
          (empresas:EmpresaXBureau) => {
            if(empresas != null) this.router.navigate([this.goBackLink]);
          }
        );*/
        break;
      case 'change':
        this.empresaBureauService.save([this.content], () => this.exit());
        /*this.empresaBureauService.save([this.content]).subscribe(
            (empresas:EmpresaXBureau[]) => {
              if(empresas != null) this.router.navigate([this.goBackLink]);
            },
        );*/
        break;
      case 'create':
        if (this.empresaBureauService.validItem(this.content.cdEmpresa.toString(), this.content.cdBureau.toString()) === 0) {
          this.empresaBureauService.include(this.content, () => this.exit());
          /*this.empresaBureauService.include(this.content).subscribe(
            (empresas:EmpresaXBureau) => {
              if(empresas != null && empresas.cdEmpresa !== 0)this.router.navigate([this.goBackLink]);
            }
          );*/
        }
        else {
          let msg = `JÃ¡ existe uma Empresa cadastrada com este cÃ³digo nesse Bureau`;
          this.toastr.error(msg, "Erro:");
        }
        break;

      default:
        break;
    }
  }

  cancel() {
    this.empresaBureauService.rollbackChanges();
    //this.content = this.originalCopy;
    //let contentId = this.content ? this.content.codigoCriterioRemuneracao : null;
    this.exit();
  }



  exit(): void {
    this.router.navigate([this.goBackLink]);
  }

}
