import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap, Router } from '@angular/router';
import { map, switchMap } from 'rxjs/operators';
import { Observable, BehaviorSubject, of, Subscription } from 'rxjs';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { EmpresaXBureau } from './models/empresa-bureau.model';
import { EmpresaBureauService } from './services/empresa-bureau.service';
import { release } from '../environments/release';

@Component({
  selector: 'lib-root',
  templateUrl: 'empresa-bureau.component.html',
  styleUrls: ['./empresa-bureau.component.scss']
})

export class EmpresaBureauComponent implements OnInit,OnDestroy {

  goBackLink: string;
  openType: string = "";
  cdBureau: string = "";
  cdEmpresa: string = "";
  faTimes = faTimes;

  showLoading: boolean = false;
  arqLoading$: Observable<boolean>;

  loadSubs: Subscription;
  release: string = release.version;

  constructor(private route: ActivatedRoute,
    private router: Router,
    private empresaBureauService: EmpresaBureauService) {

    this.arqLoading$ = this.empresaBureauService.loading$;
    this.loadSubs = this.arqLoading$.subscribe((status) => {
      this.showLoading = status;
    });

    this.goBackLink = '/external-' + this.empresaBureauService.getResourceName();
  }
  ngOnInit() {
    this.route.params.subscribe(params => {
      console.log(params);
      if (params.hasOwnProperty("openType"))
        this.openType = params.get("openType");
      if (params.hasOwnProperty("cdBureau"))
        this.cdBureau = params.get("cdBureau");
      if (params.hasOwnProperty("cdEmpresa"))
        this.cdEmpresa = params.get("cdEmpresa");
    });
  }
  
  ngOnDestroy() {
    console.log('Cleaning EmpresaBureau...');
    if (this.loadSubs != undefined) this.loadSubs.unsubscribe();
  }

}
